import React from "react";
import Tilt from 'react-parallax-tilt';
import dash from "./img/projects/dashboard.png";
import pontem from "./img/projects/pontem.png";
import vis from "./img/projects/vis.png";
import todo from "./img/projects/ToDoApp.png";
import imageprocess from "./img/projects/imageprocess.png";
import { motion, useAnimation } from "framer-motion"
import { useInView } from "react-intersection-observer";
import { useEffect } from "react";
import {text, picture, header, button, card1_3, card4_6, sectionTitle}  from "./motion";
export default function Skills() {
  const control = useAnimation();
  const [ref, inView] = useInView();
  useEffect(() => {
    if (inView) {
      control.start("visible");
    } else {
      control.start("hidden");
    }
  }, [control, inView]);


    return (
    <section className="h-font-inter bg-white" id="skills">
      <div className="h-screen pt-10 ">
      <motion.h1 ref={ref} variants={sectionTitle} initial="hidden" animate={control} 
      className="max-w-7xl mx-auto pl-10 pt-8 md:text-5xl text-2xl sm:mb-6 mb-2 text-gray-900 font-bold">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-custom-blue to-custom-purple">Skills</span>
                    </motion.h1>
        
      </div>
     
                       
    </section>
    );
    }